import {
    LOGIN_REQUEST,
    LOGIN_FAILED,
    LOGIN_SUCCESS,
    LOGOUT_REQUEST,
    LOGOUT_FAILED,
    LOGOUT_SUCCESS
} from '../actions/auth'

const initialState = {
    isAuthenticated: false,
}

const authReducer = (state = initialState, action) => {
    switch(action.type) {
        case LOGIN_REQUEST:
            return {
                ...state,
                loading: true
            }
        case LOGIN_FAILED:
            return {
                ...state,
                error: true,
                loading: false,
                isAuthenticated: false,
                token: null,
                message: action.payload
            }
        case LOGIN_SUCCESS:
            return {
                ...state,
                loading: false,
                error: false,
                message: '',
                isAuthenticated: true,
                token: action.payload.accessToken
            }
            case LOGOUT_REQUEST:
                return {
                    ...state,
                    loading: true
                }
            case LOGOUT_FAILED:
                return {
                    ...state,
                    error: true,
                    loading: false,
                    message: action.payload
                }
            case LOGOUT_SUCCESS:
                return {
                    ...state,
                    loading: false,
                    error: false,
                    isAuthenticated: false,
                    token: null
                }
        default: return state
    }
}


export default authReducer


