import React, { Component } from 'react';
import { HomeWrapper, FilterWrapper, TableWrapper, EditBtn } from './style'
import CustSelect from '../../components/CustSelect'
import { withRouter } from 'react-router';
import Table from '../../components/Table'
import Loading from '../../components/Loading'
import Alert from '../../components/Alert'
import { colors } from '../../styles/colors'
import api from '../../api'
const localStorage = require('../../utils/localStorage');
const API = api.API_ROOT
class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      cities: [{ id: 0, city: "كل المدن" }],
      categories: [{ id: 2, city: "صالات" }],
      subCategories: [],
      filteredSubCat: [],
      branches: [],
      selectedCity: 0,
      selectedCategory: 2,
      selectedSubCategory: 1,
      loading: false,
      tableHeader: [
        { name: '#', align: 'right' },
        { name: 'اسم', align: 'right' },
        { name: 'المدينة', align: 'center' },
        { name: 'القسم', align: 'center' },
        { name: 'القسم الفرعي', align: 'center' },
        { name: 'العنوان', align: 'right' },
        { name: 'الحالة', align: 'right' },
        { name: 'تعديل', align: 'center' },
      ],
      tableSettings: {
        headerBgColor: colors.maroonOak,
        headerColor: '#fff'
      },
      filters: {
        username: '0',
        city: 0,
        category: 2,
        subCategory: 0,
        page: 0,
        pageSize: 20
      },
      resError: false,
      resSuccess: false,

    }
  }

  componentDidMount() {
    const token = localStorage.getLocalStorage('accessToken');
    // if there is no token redirect to login
    if (!token) { this.props.history.push('/') }
    fetch(API + '/getcities')
      .then(response => response.json())
      .then(data => this.setState({ cities: [{ id: 0, city: "كل المدن" }, ...data.data] }));

    fetch(API + '/getcategories')
      .then(response => response.json())
      .then(data => this.setState({ categories: data.data }));

    fetch(API + '/getsubcategories')
      .then(response => response.json())
      .then(data => this.setState({ subCategories: data.data }, () => {
        let subCat = data.data.filter(item => item.id === 2)
        this.setState({ filteredSubCat: subCat[0].sub_categories.concat({ name: 'كل الاقسام', id: 0 }) })
      }));

    this.fetchBranches()

  }

  fetchBranches = (scroll) => {
    this.setState({ loading: true })
    const { filters } = this.state
    const username = filters.username.trim().length ? filters.username : '0'
    fetch(API + `/getBranches/${filters.page}/${filters.pageSize}/${filters.city}/${filters.category}/${filters.subCategory}/${username}`)
      .then(response => response.json())
      .then(data => {
        if (scroll) {
          let res = this.state.branches
          res.data = res.data.concat(data.data)
          this.setState({ branches: res, loading: false })
        } else {
          this.setState({ branches: data, loading: false })
        }
      });
  }
  handleChange = e => {
    this.setState({ resError: false, resSuccess: false, resMsg: '', loading: false })
    let temp = this.state.filters
    temp[e.target.name] = e.target.value

    if (e.target.name === 'category') {
      temp.subCategory = 0
      let subCat = this.filterSubCategories(e.target.value)
      this.setState({ filteredSubCat: subCat })
    }
    if (e.target.name !== 'username') {
      temp.page = 0
      this.setState({ filters: temp, branches: [] }, () => {
        this.fetchBranches()
      })
    } else {
      this.setState({ filters: temp })
    }
  }

  onScroll = () => {
    let temp = this.state.filters
    const { branches } = this.state
    let page = temp.page

    if (!(page >= branches.pageCount)) {
      temp.page = page + 1
      this.setState({ filters: temp }, () => {
        this.fetchBranches(true)
      })
    }
  }

  filterSubCategories = (id) => {
    let subCat = this.state.subCategories.filter(item => item.id === Number(id))
    return subCat[0].sub_categories.concat({ name: 'كل الاقسام', id: 0 })
  }

  search = () => {
    let temp = this.state.filters
    temp.page = 0
    this.setState({ filters: temp, branches: [] }, () => {
      this.fetchBranches()
    })
  }

  handleBranchChange = async (branchId, e) => {
    this.setState({ loading: true })
    let temp = this.state.branches
    const index = temp.data.findIndex(item => item.branch_id === branchId)
    const body = {
      field: e.target.name,
      value: e.target.value,
      branch_id: branchId
    }
    temp.data[index][e.target.name] = e.target.value
    const response = await api.Branches.instantEdit(body)
    if (response.statusCode === 200) {
      this.setState({ branches: temp, resSuccess: true, resMsg: response.message, loading: false })
    } else if (response.statusCode === 403 || response.statusCode === 500) {
      this.setState({ resError: true, resMsg: response.statusName, loading: false })
      localStorage.removeLocalStorage('accessToken')
      this.props.history.push('/')
    }
  }

  renderTableData = () => {
    const { branches, categories, cities } = this.state
    let data = []
    branches.data && branches.data.map((branch, i) => {
      data.push(

        <tr key={i}>
          <td width='10px' align='right'>{i + 1}</td>
          <td width='300px' align='right'>{branch.branch_name}</td>
          <td width='100px' align='center'>
            <CustSelect
              value={branch.city_id}
              name='city_id'
              onChange={(e) => this.handleBranchChange(branch.branch_id, e)}
              options={cities.filter(item => item.id != 0).map((item) => { return { name: item.city, id: item.id } })} />
          </td>
          <td width='100px' align='center'>
            <CustSelect
              value={branch.category_id}
              name='category_id'
              onChange={(e) => this.handleBranchChange(branch.branch_id, e)}
              options={categories.map((item) => { return { name: item.type_name, id: item.id } })} />
          </td>
          <td width='100px' align='center'>
            <CustSelect
              value={branch.sub_cat_id}
              name='sub_cat_id'
              onChange={(e) => this.handleBranchChange(branch.branch_id, e)}
              options={this.filterSubCategories(branch.category_id).filter(item => item.id != 0)} />
          </td>
          <td align='right'>{branch.address}</td>
          <td align='right'>{branch.status ? 'نشط' : 'غير نشط'}</td>
          <td align='center'><EditBtn>تعديل</EditBtn></td>
        </tr>

      )
    })
    return data
  }

  render() {
    const { cities, categories, tableSettings, filters, filteredSubCat, tableHeader, loading, resError, resSuccess, resMsg } = this.state

    return (

      <HomeWrapper>
        <FilterWrapper dir="rtl">
          <input placeholder='اسم مقدم الخدمة' dir="rtl" autocomplete="off" name='username' value={filters.username != '0' ? filters.username : ''} onChange={this.handleChange} />
          <button onClick={this.search}>بحث</button>
          <CustSelect
            value={filters.city}
            name='city'
            onChange={this.handleChange}
            options={cities.map((item) => { return { name: item.city, id: item.id } })} />
          <CustSelect
            value={filters.category}
            name='category'
            onChange={this.handleChange}
            options={categories.map((item) => { return { name: item.type_name, id: item.id } })} />
          <CustSelect
            value={filters.subCategory}
            name='subCategory'
            onChange={this.handleChange}
            options={filteredSubCat} />
          {resError ? <Alert type={'danger'} message={resMsg} /> : ''}
          {resSuccess ? <Alert type={'success'} message={resMsg} /> : ''}

        </FilterWrapper>
        {loading && <Loading />}
        <TableWrapper>
          <Table
            headerContent={tableHeader}
            tableSettings={tableSettings}
            loading={loading}
            tableData={this.renderTableData()}
            handleWaypointEnter={this.onScroll} />
        </TableWrapper>

      </HomeWrapper>
    );
  }
}

export default withRouter(Home);


