import React, { Component, useCallback } from 'react';
import styled from 'styled-components'
import CustomInput from '../../components/CustomInput'
import Loading from '../../components/Loading'
import Button from '../../components/Button'
import Alert from '../../components/Alert'
import { withRouter } from 'react-router';
import logo from '../../../assets/logo.png'
import { colors } from '../../styles/colors'
import { connect } from 'react-redux'
import { loginUser } from '../../actions/auth'
import 'bootstrap/dist/css/bootstrap.css';
const localStorage = require('../../utils/localStorage');

export const LoginWrapper = styled.div`
  position: absolute;
  background-color:${colors.white};
    top: -70px;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align:right;
    .inner-wrpper {
        display: flex;
        flex-direction: column;
        width: 20%;
        min-width: 250px;
    }
    .inner-wrpper:before{ 
    background: #EBEBEB;
    display: flex;
    flex-direction: column;
    width: 20%;
    min-width: 250px;
   
    }
    p {
      color:${colors.doveGray};
      margin-left: auto;
      font-size: 20px;
    }
   
`
const HeaderLogo = styled.img`
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
`


class Login extends Component {

  constructor(props) {
    super(props)
    this.state = {
      email: '',
      password: '',
      emailErr: false,
      passwordErr: false,
      loading: false
    }
  }
  componentDidMount() {
    const token = localStorage.getLocalStorage('accessToken');
    // if there is no token redirect to login
    if (token) { this.props.history.push('/Home') }
  }

  UNSAFE_componentWillReceiveProps(nextprops) {
    if (this.props.loading) {
      this.setState({ loading: false })

    }
    if (nextprops.auth.token) {

      localStorage.saveLocalStorage('accessToken', nextprops.auth.token)
      this.props.history.push('../Home')
    } else {

    }
  }
  handleUserLogin = e => {
    this.setState({ loading: true })

    e.preventDefault()
    const { email, password } = this.state



    if (email && password) {

      this.props.loginUser(email, password)

    }

  }
  validateEmail(email) {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }
  onChange = e => {

    this.setState({ [e.target.name]: e.target.value }, () => {
      const { email, password } = this.state
      if (email.length && this.validateEmail(email)) {
        this.setState({ emailErr: null })

      } else {
        this.setState({ emailErr: 'بريد غير صحيح' })
      }

      if (password.length && password.length > 5) {
        this.setState({ passwordErr: null })
      } else {

        this.setState({ passwordErr: 'كلمة المرور يجب آن تكون أكثر من ٨ خانات' })
      }
    })

  }
  errFree = () => {
    const { passwordErr, emailErr } = this.state
    if (passwordErr !== null || emailErr !== null) {
      return false
    } else {
      return true
    }
  }
  render() {

    const { email, password, emailErr, passwordErr, loading } = this.state
    const { auth } = this.props

    return (
      <LoginWrapper>

        <div className='inner-wrpper'>


          <HeaderLogo src={logo} alt="happy-season" />
          {loading ? <Loading /> : ''}
          <p>مرحبا بك في لوحة التحكم الخاصة بتطبيق هابي سيزون قم بتسجيل الدخول لتتمكن من ادارة محتوى التطبيق </p>

          {auth.error ? <Alert type={'danger'} message={auth.message} /> : ''}
          <CustomInput type='email' error={emailErr} name='email' label='البريد الإلكتروني' value={email} onChange={this.onChange} />
          <CustomInput type={'password'} error={passwordErr} name='password' label='كلمة المرور' value={password} onChange={this.onChange} />
          <Button label='تسجيل الدخول' onClick={this.handleUserLogin} disable={this.errFree()}></Button>

          <a>نسيت كلمة المرور</a>


        </div>
      </LoginWrapper>
    )
  }
}

const mapStateToProps = state => ({
  auth: state.auth,
  loading: state.auth.loading,
  error: state.auth.error
})


const mapDispatchToProps = dispatch => ({
  loginUser: (email, password) => dispatch(loginUser(email, password))
})
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));


