import React from 'react'
import styled from 'styled-components'
const SelectWrapper = styled.div`
    display: flex;
    margin: 0 5px;
    select{
        height:35px;
        line-height:35px;
        min-width: 70px;
        border-radius: 5px;
    }
  
`
class CustSelect extends React.PureComponent {

    render() {
        const { onChange, options, value, name, placeholder } = this.props
        return (
            <SelectWrapper>
                <select disabled={options && !options.length} placeholder={placeholder}dir="rtl" name={name} onChange={onChange} value={value}>
                    {options && options.length ? options.map((item, i) => {
                        return (
                            <option key={i} value={item.id}>{item.name}</option>
                        )
                    }) : <option >لا يوجد خيار</option>}
                </select>
            </SelectWrapper>
        )
    }
}




export default CustSelect


