import React from 'react';
import { StyledTable, Wrapper } from './style'
import { Waypoint } from 'react-waypoint';
import Loading from '../Loading'
/************************************ READ TABLE SETTINGS PROPS BELOW FOR BETTER CONFIGURATIONS ************************/
const Table = (props) => {

    const { headerContent, tableData, handleWaypointEnter, totalContent, tableSettings, loading } = props
    const tableHeight = tableSettings ? tableSettings.tableMaxHeight : '650px'
    return (
        <Wrapper tableHeight={tableHeight}>
            <StyledTable tableSettings={tableSettings}>

                <thead>
                    {headerContent && headerContent.map((header, i) => {
                        return (
                            <td data-label={header.name} align={header.align} key={i}>{header.name}</td>
                        )
                    })}
                </thead>

                {tableData}
              


                {totalContent &&
                    <tfoot>
                        {totalContent}
                    </tfoot>
                }
                {loading && <Loading/>}
            </StyledTable>
            <Waypoint onEnter={handleWaypointEnter} />
        </Wrapper>
    )
}

export default Table


/***

 ***** TableSettings props *****

headerBgColor: color (default: #580606)
headerColor: color (default: #fff)
headerFontSize:`14px`,
contentFontSize:`14px`
contentColor:black
contentBgColorOdd:white
contentBgColorEvens:#f2f2f2



***/


