import api from '../../api'
const localStorage = require( '../../utils/localStorage');


export const LOGIN_REQUEST = 'LOGIN_REQUEST'
export const LOGIN_FAILED = 'LOGIN_FAILED'
export const LOGIN_SUCCESS = 'LOGIN_SUCCESS'
export const LOGOUT_REQUEST = 'LOGOUT_REQUEST'
export const LOGOUT_SUCCESS = 'LOGOUT_SUCCESS'
export const LOGOUT_FAILED = 'LOGOUT_FAILED'

export const loginUser = (email, password) => {
    return async dispatch => {

        dispatch({ type: LOGIN_REQUEST })

        const result = await api.Auth.login({email: email, password: password})
        
        if (result.error || result.status === 401) {
            dispatch({ type: LOGIN_FAILED, payload: result.data.statusName || result.statusMessage })
        } else if (result.statusCode === 200) {            
            dispatch({
                type: LOGIN_SUCCESS,
                payload: result.data                
            })
        }
    }
}

export const logoutUser = () => {
    return async dispatch => {
        const accessToken = localStorage.getLocalStorage('accessToken')
        dispatch({ type: LOGOUT_REQUEST })  
        const result = await api.Auth.logout({accessToken: accessToken}) 
        if (result.error || result.status === 401) {
            dispatch({ type: LOGOUT_FAILED, payload: result.data.statusName || result.statusMessage })
        } else if (result.statusCode === 200) {     
            localStorage.removeLocalStorage('accessToken')       
            dispatch({
                type: LOGOUT_SUCCESS,
                payload: result.data                
            })

        }     
       
       
    }
} 


