import styled from 'styled-components'
import {colors} from '../../styles/colors'
export const HomeWrapper = styled.div`
    display: flex;
    flex-direction:column;
    width: 100%;
    height: 100%;

`

export const FilterWrapper = styled.div`
    display: flex;
    height: 50px;
    width: 100%;
    padding: 10px;
    input{
        height: 30px;
    }
    button{
        height: 35px;
        margin-right:5px;
        color: white;
        background-color: ${colors.maroonOak};
        border:0;
        padding: 0 25px;
    }
`

export const TableWrapper = styled.div`
    display: flex;
    height: 800px;
   

`
export const EditBtn = styled.button`
    background-color: ${colors.maroonOak40};
    color: white;
    font-size: 1em;
    padding: 0.25em 1em;
    border: 2px solid ${colors.maroonOak};
    border-radius: 4px;
    width: 50%;
  
   

`


