import React, { Component } from 'react';
import styled from 'styled-components'
import { colors } from '../../styles/colors'
const Wrapper = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    margin-top: 22px;
    & label {
        position: absolute;
        margin-left: 10px;
        color: ${colors.dustyGray};
        font-size: 14px;
        font-family: 'Roboto';
        transition: all .20s ease-in-out;
        right:5px;
      }
      & input {
        box-sizing: content-box;
        height: 45px;
        border-radius: 4px;
        border: 1px solid ${colors.lightGray} ;
        padding: 0 5px;
        font-family: 'Roboto';
        font-size: 16px;
        outline: none;
      }
       
    & input:valid {
        background: rgba(255, 255, 255, 0.1);
        color: black;
    }
    
    & input:focus {
        border: 2px solid ${colors.maroonOak};
    }
    
    & input:focus + label {
        transform: translate(0, -35px);
        color: ${colors.gray};
    }
    
    & input:valid + label {
        transform: translate(0, -35px);
        color: black;
    }
    
    & input:focus ~ i {
        opacity: 1;
    }
    
    & input:valid ~ i {
        color: black;
    }
`
const Error = styled.span` 
    display: inline-block;
    width: 100%;
    font-size: 16px;
    color: ${colors.maroonOak90};
    text-align: right;
    
`
class CustomInput extends Component {

    render(){     
        const { label, name, onChange, value, error, type } = this.props
        const inputType = type ? type:'text'
        return(
            <div>
            <Wrapper>
                    <input dir="rtl" autocomplete="off" name={name} value={value} onChange={onChange} id="field" type={inputType} required/>
                    <label id="label">{label}</label>
                    
            </Wrapper>
            { error && <Error>{error}</Error>}
            </div>
        )
    }
}

export default CustomInput


