import styled from 'styled-components'
export const Wrapper = styled.div`
          position: relative;
          width:100%;
          z-index: 1;
          overflow: auto;
          height: 90%;
          z-index:0;
   
 
`
export const StyledTable = styled.table`
          direction:rtl;
          width: 100%;
          margin: auto;
          border-collapse: separate;
          border-spacing: 0;
          th,  td {
          padding: 5px 10px;
          border: 1px solid #ddd;
          background: #fff;
          vertical-align: top;
          }
          thead th,  thead td{
               background: ${props => props.tableSettings && props.tableSettings.headerBgColor ? props.tableSettings.headerBgColor  :'#fff'};
               color: ${props => props.tableSettings && props.tableSettings.headerColor ? props.tableSettings.headerColor  :'#000'};   
               position: -webkit-sticky;
               position: sticky;
               font-weight: 700;
               font-size:${props => props.tableSettings && props.tableSettings.headerFontSize ? props.tableSettings.headerFontSize  :'14px'};
               top: 0;
          }
          tr td {
      
               white-space: nowrap;
               z-index:2;
               font-size:${props => props.tableSettings && props.tableSettings.contentFontSize ? props.tableSettings.contentFontSize  :'14px'};
               color: ${props => props.tableSettings && props.tableSettings.contentColor ? props.tableSettings.contentColor  :'#000'};
           
       
          } 
          tfoot, tfoot th, tfoot td {
               position: -webkit-sticky;
               position: sticky;
               bottom: 0;
               background: #666;
               color: #fff;
               z-index:4;
               font-size: 16px;
               font-weight: 700; 
               background: ${props => props.tableSettings && props.tableSettings.headerBgColor ? props.tableSettings.headerBgColor  :'#fff'};
               color: ${props => props.tableSettings && props.tableSettings.headerColor ? props.tableSettings.headerColor  :'#000'};   
          }

          @media screen and (max-width: 600px) {
             
              thead {
               border: none;
               clip: rect( 0 0 0 0);
               height: 1px;
               margin: -1px;
               overflow: hidden;
               padding: 0;
               display: none;
               width: 1px;
               z-index:99;
             }
             
              tr {
               border: 2px solid #000;
               display: block;
               margin-bottom: .625em;
             }
             
              td {
               border-bottom: 1px solid #ddd;
               display: block;
               border-left:2px solid #000;
               font-size: .8em;
               text-align: right;
               width: 94% !important;
             }
             
              td::before {
               /*
               * aria-label has no advantage, it won't be read inside a table
               content: attr(aria-label);
               */
               content: attr(data-label);
               float: right;
               font-weight: bold;
               text-transform: uppercase;
             }
             
              td:last-child {
               border-bottom: 0;
             }
           }

   
 
`






