import React from 'react'
import { faBars, faSignOutAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { withRouter } from 'react-router';
import styled from 'styled-components'
import api from '../../../api'
import { connect } from 'react-redux'
import { logoutUser } from '../../../actions/auth'
const localStorage = require('../../../utils/localStorage');
import { colors } from '../../../styles/colors'
const HeaderWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    position: fixed;
    z-index: 1020;
    width: 100%;
    background: ${colors.maroonOak90};
    height: 55px;
    padding: 0 0;
    margin: 0;
    box-sizing: border-box;
    .nav{
        cursor: pointer;
        display: flex;
        margin : 10px;
        span{
            color: white;
            display: inline-block;
            margin-left: 20px;
            font-size: 18px;
            font-weight: 700;
       
        }
        svg{
            color: white;
            margin-left: 10px;
            
        }
    }
    .log-out{
        cursor: pointer;
        display: flex;
        margin : 10px;
        svg{
            color: white;
            margin-left: 10px;
            
        }

    }
   
`
class Header extends React.PureComponent {
    constructor(props) {
        super(props);
        this.logOut = this.logOut.bind(this);

    }
    logOut = () => {
        this.props.logoutUser()
    }
    UNSAFE_componentWillReceiveProps(nextprops) {

        if (!nextprops.auth.isAuthenticated) {


            this.props.history.push('/')
        }
    }

    render() {
        const { navClicked, isLoginPage } = this.props
        return (
            <HeaderWrapper>
                <div className="log-out">
                    {!isLoginPage && <FontAwesomeIcon onClick={this.logOut} size='lg' icon={faSignOutAlt} />}
                </div>
                <div className="nav">
                    <span>لوحة تحكم هابي سيزون</span>
                    {!isLoginPage && <FontAwesomeIcon onClick={navClicked} size='lg' icon={faBars} />}

                </div>

            </HeaderWrapper>
        )
    }
}


const mapStateToProps = state => ({
    auth: state.auth,
    loading: state.auth.loading,
    error: state.auth.error
})
const mapDispatchToProps = dispatch => ({
    logoutUser: () => dispatch(logoutUser())
})
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header))


